}

.-preview .m__hero img {
  max-width: 100%;
}
.m__hero__hero {
  position: relative;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
}
.-preview .m__hero__hero {
  min-height: 600px;
}
.m__hero__hero .img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.m__hero__hero .img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.m__hero .wrapper {
  position: relative;
}

.m__scText {
  padding: 100px 0;
